import { AxesHelper, GridHelper, Object3D } from 'three';

/**
 * 
 */
export default class Plane extends Object3D {
  /**
   * 
   * @constructor
   */
  constructor() {
    super();

    // 
    const gridHelper = new GridHelper(20, 10, 0xffffff, 0xffffff);
    this.add(gridHelper);

    // Axis
    const axisHelper = new AxesHelper(5);
    this.add(axisHelper);
  }
}
